import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: () => import('@/components/Home')
    },
    {
      path: '/education',
      name: 'education',
      component: () => import('@/components/Education')
    },
    {
      path: '/experience',
      name: 'experience',
      component: () => import('@/components/Experience')
    },
    {
      path: '/projects',
      name: 'projects',
      component: () => import('@/components/Projects')
    },
    {
      path: '/volunteer',
      name: 'volunteer',
      component: () => import('@/components/Volunteer')
    },

  ]
})
